<template>
  <van-popup
    v-model:show="isShow"
    :style="{ width: '88%', background: 'none' }"
    :close-on-click-overlay="false"
  >
    <!-- <div class="popup-style">
      <div class="popup-header">{{$t('請填寫推薦人')}}</div>
      <div class="input-box">
        <van-field type="text" :placeholder="$t('請輸入你的推薦人錢包地址')" input-align="center" v-model="inputReferer" />
      </div>
      <div class="popup-btns flex-center">
        <van-button 
          type="primary"
          class="btn" 
          :disabled="loading"
          :loading="loading" 
          :loading-text="$t('確定')" 
          @click="toReg" 
        >{{$t('確定')}}</van-button>
      </div>
    </div> -->
    <div class="popup-box">
      
      <div class="invitations">
        <p class="invitations-title">{{ $t("index.邀请人") }}</p>
        <textarea v-model="inputReferer" rows="2" class="links"></textarea>
      </div>
      <van-button type="primary" :loading="introLoading" round block  @click="toReg">{{
        $t("index.确定")
      }}</van-button>
    </div>
  </van-popup>
</template>
<script>
import { reactive, toRefs, computed } from "vue";
import { Toast } from "vant";
import { useI18n } from "vue-i18n";
export default {
  props: {
    showIntro: {
      type: Boolean,
      defalut: false,
    },
    intro: {
      type: String,
    },
    introLoading: {
      type: Boolean,
      defalut: false,
    },
  },
  emits: ["update:showIntro", "toReg", "update:introLoading"],
  setup(props, { emit }) {
    const { t } = useI18n();
    const data = reactive({
      isShow: computed({
        get() {
          return props.showIntro;
        },
        set(value) {
          emit("update:showIntro", value);
        },
      }),
      loading: computed({
        get() {
          return props.introLoading;
        },
        set(value) {
          emit("update:introLoading", value);
        },
      }),
      inputReferer: props.intro || "",
      toReg: () => {
        if (!data.inputReferer) {
          Toast(t("index.请输入邀请地址"));
          return;
        }
        emit("toReg", data.inputReferer);
      },
    });
    const refData = toRefs(data);
    return {
      ...refData,
    };
  },
};
</script>
